import React, { useState } from 'react';
import Layout from '../../components/layout/layout';
import QueueCanvas from '../../components/dataCanvas/QueueCanvas';
import DataCanvasUI from '../../components/dataCanvas/DataCanvasUI';

const QueuePage: React.FC = () => {
  const [queue, setQueue] = useState<string[]>([]);
  const [inputValue, setInputValue] = useState('');
  const [queueSize, setQueueSize] = useState<number>(0);
  const [isAdding, setIsAdding] = useState(false);
  const [isRemoving, setIsRemoving] = useState(false);

  const handlePush = () => {
    if (!inputValue.trim()) {
      alert('  .');
      return;
    }
    if (queue.length < queueSize || queueSize === 0) {
      setQueue(prevQueue => {
        setIsAdding(true);
        return [...prevQueue, inputValue];
      });
      setInputValue('');
      setTimeout(() => {
        setIsAdding(false);
      }, 500);
    } else {
      alert('  .');
    }
  };

  const handlePop = () => {
    if (queue.length > 0) {
      setIsRemoving(true);
      const newQueue = [...queue];
      setTimeout(() => {
        newQueue.shift();
        setQueue(newQueue);
        setIsRemoving(false);
      }, 500);
    } else {
      alert('  .');
    }
  };

  const handleQueueSizeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newSize = Number(event.target.value);
    setQueueSize(newSize);
    setQueue([]);
  };

  return (
    <Layout subTitle="(QUEUE)">
      <QueueCanvas 
        queue={queue}
        isAdding={isAdding}
        isRemoving={isRemoving}
      />
      <DataCanvasUI
        data={queue}
        inputValue={inputValue}
        maxSize={queueSize}
        setInputValue={setInputValue}
        setMaxSize={setQueueSize}
        handlePush={handlePush}
        handlePop={handlePop}
        handleMaxSizeChange={handleQueueSizeChange}
        addItemPlaceholder="  " 
        sizePlaceholder=" "
      />
    </Layout>
  );
};

export default QueuePage;
